<template>
    <div class="rigister-wrap">
        <login-top middleMsg="">
            <div slot="rightMsg" style="font-size: 4.167vw" @click="$router.push('/register')"></div>    
        </login-top> 
        
        <login-text 
            label="" 
            class="login-text"
            placeholder=""
            rule="^.{6,16}$"
            @inputChange="arg => username = arg"
        ></login-text>  
        <login-text 
            label="" 
            class="login-text"
            placeholder=""
            type="password"
            rule="^.{6,16}$"
            @inputChange="arg => password = arg"
        ></login-text>  
        
        <login-btn btntext="" @submit="handleSubmit"></login-btn>

    </div>
</template>

<script>
import LoginTop from '@/components/common/LoginTop.vue';
import LoginText from '@/components/common/LoginText.vue';
import LoginBtn from '@/components/common/LoginBtn.vue';

export default {
    components: {
        LoginTop,
        LoginText,
        LoginBtn
    },
    data() {
        return {
            //
            username: '',
            //
            password: '',
        }
    },
    methods: {
        /**
        * @func
        * @desc 
        * @param {String} arg - inputChange
        */
        handleInputChange(arg) {
            this.name = arg;
        },
        /**
        * @func
        * @desc 
        */
        handleSubmit() {
            if (this.username && this.password) {
                //,
                this.$http.post('/login', {
                    name: this.name,
                    username: this.username,
                    password: this.password
                }).then(res => {
                    console.log(res);
                    let data = res.data;

                    if (data) {
                        //data
                        if (data.code === 302 || data.code === 301) {
                            //302
                            this.$toast.fail(data.msg);
                        }
                        else if (data.code === 200) {
                            //200
                            this.$toast.success(data.msg);

                            //idtoken
                            localStorage.setItem("id", data.id);
                            sessionStorage.setItem("token", data.token);

                            //userinfo
                            setTimeout(() => {
                                this.$router.push('/userinfo');
                            }, 1000);
                        }
                        else {
                            //
                            this.$toast(data.msg);
                        }
                    }
                }).catch(err => {
                    console.log(err);
                });
            }
            else {
                this.$toast.fail("");
            }
        }
    }
}
</script>

<style lang='scss' scoped>
.rigister-wrap {
    .login-text {
        margin: 2.778vw 0;
        font-size: 4.167vw;
    }
}
</style>